import { Children, FC } from 'react';

type ListProps = {
  isOpen?: boolean;
}

const List: FC<ListProps> = (props) => {
  const { isOpen, children } = props;

  const kids = Children.map(children, (child) =>
    <li className={`nav-item flex-grow-0 my-1 mx-1 ${isOpen ? 'collapse-show' : 'collapse'} navbar-collapse`}>
      {child}
    </li>
  );

  return (
    <ul className="ms-auto me-auto me-md-0 w-md-100 navbar-nav navbar-nav-hovers ms-md-2 flex-row me-md-auto">
      {kids}
    </ul>
  );
};

export default List;
